<template>
  <div class="system-page py-5">
    <div class="container">
      <!-- 页面标题 -->
      <div class="row mb-5">
        <div class="col-12 text-center">
          <h1 class="display-5 fw-bold mb-3">智能船艇产品</h1>
          <p class="lead text-secondary">我们提供全系列无人船艇产品，适用于水质监测、海洋巡逻和应急救援等场景</p>
        </div>
      </div>

      <!-- 产品分类筛选 -->
      <div class="row mb-4">
        <div class="col-12">
          <div class="category-filter d-flex flex-wrap justify-content-center gap-3">
            <button class="btn" :class="activeFilter === 'all' ? 'btn-primary' : 'btn-outline-primary'" @click="setFilter('all')">
              全部产品
            </button>
            <button class="btn" :class="activeFilter === 'single' ? 'btn-primary' : 'btn-outline-primary'" @click="setFilter('single')">
              <i class="bi bi-tsunami me-1"></i>单人船
            </button>
            <button class="btn" :class="activeFilter === 'sampling' ? 'btn-primary' : 'btn-outline-primary'" @click="setFilter('sampling')">
              <i class="bi bi-droplet me-1"></i>采样船
            </button>
            <button class="btn" :class="activeFilter === 'patrol' ? 'btn-primary' : 'btn-outline-primary'" @click="setFilter('patrol')">
              <i class="bi bi-shield me-1"></i>巡逻船
            </button>
            <button class="btn" :class="activeFilter === 'firefighting' ? 'btn-primary' : 'btn-outline-primary'" @click="setFilter('firefighting')">
              <i class="bi bi-fire me-1"></i>消防船
            </button>
          </div>
        </div>
      </div>

      <!-- 产品展示 -->
      <div class="row g-4">
        <div class="col-12 col-md-6 col-lg-4" v-for="boat in filteredBoats" :key="boat.id">
          <div class="card h-100 product-card">
            <div class="product-image">
              <img :src="boat.image" class="card-img-top" :alt="boat.name">
              <div class="product-overlay">
                <button class="btn btn-light" @click="showDetails(boat)">查看详情</button>
              </div>
            </div>
            <div class="card-body">
              <div class="product-badge" :class="getBadgeClass(boat.type)">
                {{ getTypeName(boat.type) }}
              </div>
              <h5 class="card-title">{{ boat.name }}</h5>
              <p class="card-text">{{ boat.description }}</p>
              <div class="specs">
                <div class="specs-item" v-for="(key, index) in getMainSpecs(boat)" :key="index">
                  <span class="specs-label">{{ key }}:</span>
                  <span class="specs-value">{{ boat.specifications[key] }}</span>
                </div>
              </div>
            </div>
            <div class="card-footer bg-white border-top-0">
              <div class="d-flex gap-2">
                <button class="btn btn-primary flex-grow-1" @click="showDetails(boat)">产品详情</button>
                <button class="btn btn-outline-primary flex-grow-1">获取报价</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- 产品详情模态框 -->
    <div class="modal fade" id="boatModal" tabindex="-1">
      <div class="modal-dialog modal-xl modal-dialog-scrollable">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">{{ selectedBoat ? selectedBoat.name : '' }}</h5>
            <button type="button" class="btn-close" @click="closeModal" aria-label="Close"></button>
          </div>
          <div class="modal-body" v-if="selectedBoat">
            <!-- 产品导航标签 -->
            <ul class="nav nav-tabs mb-4" id="productTab" role="tablist">
              <li class="nav-item" role="presentation">
                <button class="nav-link active" id="overview-tab" data-bs-toggle="tab" data-bs-target="#overview" type="button" role="tab">产品概述</button>
              </li>
              <li class="nav-item" role="presentation">
                <button class="nav-link" id="specs-tab" data-bs-toggle="tab" data-bs-target="#specs" type="button" role="tab">技术参数</button>
              </li>
              <li class="nav-item" role="presentation">
                <button class="nav-link" id="features-tab" data-bs-toggle="tab" data-bs-target="#features" type="button" role="tab">特点优势</button>
              </li>
              <li class="nav-item" role="presentation">
                <button class="nav-link" id="applications-tab" data-bs-toggle="tab" data-bs-target="#applications" type="button" role="tab">应用场景</button>
              </li>
              <li class="nav-item" role="presentation" v-if="selectedBoat.video">
                <button class="nav-link" id="video-tab" data-bs-toggle="tab" data-bs-target="#video" type="button" role="tab">产品视频</button>
              </li>
            </ul>

            <!-- 标签内容 -->
            <div class="tab-content" id="productTabContent">
              <!-- 产品概述 -->
              <div class="tab-pane fade show active" id="overview" role="tabpanel">
                <div class="row">
                  <div class="col-lg-6">
                    <div class="product-gallery">
                      <div class="main-image mb-3">
                        <img :src="selectedBoat.image" class="img-fluid rounded" :alt="selectedBoat.name">
                      </div>
                      <div class="row g-2">
                        <div class="col-3" v-for="(img, index) in selectedBoat.gallery" :key="index">
                          <img :src="img" class="img-fluid rounded" :alt="'产品图片 ' + (index + 1)">
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="col-lg-6">
                    <div class="product-badge mb-3" :class="getBadgeClass(selectedBoat.type)">
                      {{ getTypeName(selectedBoat.type) }}
                    </div>
                    <h3 class="product-title mb-3">{{ selectedBoat.name }}</h3>
                    <p class="product-description mb-4">{{ selectedBoat.description }}</p>
                    
                    <h5 class="mb-3">主要特点</h5>
                    <div class="row g-3 mb-4">
                      <div class="col-lg-6" v-for="(feature, index) in selectedBoat.features.slice(0, 4)" :key="index">
                        <div class="feature-item d-flex align-items-center">
                          <div class="feature-icon me-2 text-accent">
                            <i class="bi bi-check-circle-fill"></i>
                          </div>
                          <div class="feature-text">{{ feature }}</div>
                        </div>
                      </div>
                    </div>
                    
                    <h5 class="mb-3">关键参数</h5>
                    <div class="row g-3">
                      <div class="col-6" v-for="(key, index) in getMainSpecs(selectedBoat)" :key="index">
                        <div class="spec-box p-3 rounded bg-light">
                          <div class="spec-title text-secondary small">{{ key }}</div>
                          <div class="spec-value fw-bold">{{ selectedBoat.specifications[key] }}</div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>

              <!-- 其他标签内容与无人机产品页面类似，这里省略 -->

            </div>
          </div>
          <div class="modal-footer">
            <div class="row w-100">
              <div class="col-lg-6 text-lg-start text-center mb-3 mb-lg-0">
                <a href="#" class="btn btn-outline-secondary me-2">下载产品手册</a>
                <a href="#" class="btn btn-outline-secondary">产品参数表</a>
              </div>
              <div class="col-lg-6 text-lg-end text-center">
                <button type="button" class="btn btn-secondary me-2" @click="closeModal">关闭</button>
                <button type="button" class="btn btn-primary">获取报价</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
