          ],
          specifications: {
            '船体材质': '碳纤维复合材料',
            '尺寸': '长1.8m×宽0.8m×高0.4m',
            '重量': '25kg',
            '最大载重': '120kg',
            '续航时间': '4小时',
            '最大速度': '15km/h',
            '通信距离': '1.5km',
            '防护等级': 'IP67',
            '工作温度': '-10℃ ~ 50℃',
            '电池容量': '48V 40Ah'
          },
          applications: ['个人休闲', '水上钓鱼', '摄影取景', '短途运输'],
          video: 'https://www.youtube.com/embed/dQw4w9WgXcQ'
        },
        {
          id: 'b02',
          type: 'sampling',
          name: 'AquaTest 水质采样船',
          description: '专业水质采样无人船，配备多种采样设备，适用于湖泊、河流等水域的水质监测',
          image: '/images/products/boats/aquatest/main.jpg',
          gallery: [
            '/images/products/boats/aquatest/detail-1.jpg',
            '/images/products/boats/aquatest/detail-2.jpg',
            '/images/products/boats/aquatest/detail-3.jpg'
          ],
          features: [
            '高精度多参数水质传感器',
            '自动采样系统，可收集多点样本',
            '数据实时传输，支持4G/5G网络',
            '远程控制与自主导航双模式',
            '高稳定性船体设计，适应各种水况',
            '太阳能辅助供电，延长工作时间',
            '便携式设计，一人即可操作部署',
            'GPS定位与轨迹记录'
          ],
          specifications: {
            '船体材质': '工程塑料+铝合金',
            '尺寸': '长1.2m×宽0.7m×高0.5m',
            '重量': '35kg',
            '最大载重': '20kg',
            '续航时间': '8小时',
            '最大速度': '8km/h',
            '通信距离': '5km',
            '防护等级': 'IP68',
            '工作温度': '-20℃ ~ 60℃',
            '采样容量': '12个500ml样本'
          },
          applications: ['水质监测', '环境调查', '科学研究', '污染源追踪'],
          video: 'https://www.youtube.com/embed/dQw4w9WgXcQ'
        },
        {
          id: 'b03',
          type: 'patrol',
          name: 'SeaGuard 海上巡逻船',
          description: '自主巡逻无人船，配备高清摄像头和声呐系统，适用于港口、海岸和敏感水域的监控任务',
          image: '/images/products/boats/seaguard/main.jpg',
          gallery: [
            '/images/products/boats/seaguard/detail-1.jpg',
            '/images/products/boats/seaguard/detail-2.jpg',
            '/images/products/boats/seaguard/detail-3.jpg'
          ],
          features: [
            '全天候自主巡逻能力',
            '高清摄像头和热成像系统',
            '水下声呐探测系统',
            '船体防撞设计，安全可靠',
            '自动避障和紧急停止功能',
            '多天续航，配备太阳能充电',
            '强大动力系统，适应复杂海况',
            '实时视频传输和指挥系统'
          ],
          specifications: {
            '船体材质': '玻璃钢+铝合金',
            '尺寸': '长4.5m×宽1.8m×高1.2m',
            '重量': '380kg',
            '最大载重': '150kg',
            '续航时间': '48小时',
            '最大速度': '45km/h',
            '巡航速度': '15km/h',
            '通信距离': '15km',
            '防护等级': 'IP68',
            '工作温度': '-30℃ ~ 70℃'
          },
          applications: ['海岸巡逻', '港口监控', '边境安保', '反走私监控', '海上救援'],
          video: 'https://www.youtube.com/embed/dQw4w9WgXcQ'
        },
        {
          id: 'b04',
          type: 'firefighting',
          name: 'FireBoat 消防救援船',
          description: '专业消防无人船，配备高压水炮和灭火设备，适用于水上消防和应急救援任务',
          image: '/images/products/boats/fireboat/main.jpg',
          gallery: [
            '/images/products/boats/fireboat/detail-1.jpg',
            '/images/products/boats/fireboat/detail-2.jpg',
            '/images/products/boats/fireboat/detail-3.jpg'
          ],
          features: [
            '大流量高压消防水炮',
            '耐高温船体设计',
            '远程遥控操作系统',
            '热成像探测器，识别火源',
            '大容量水箱和泡沫箱',
            '强大动力系统，快速反应',
            '应急照明和警报系统',
            '自动返航和安全保护功能'
          ],
          specifications: {
            '船体材质': '耐高温复合材料',
            '尺寸': '长5.2m×宽2.2m×高1.5m',
            '重量': '620kg',
            '水炮流量': '1200L/min',
            '水炮射程': '60m',
            '水箱容量': '1000L',
            '续航时间': '3小时',
            '最大速度': '35km/h',
            '通信距离': '3km',
            '防护等级': 'IP67'
          },
          applications: ['海上消防', '港口消防', '水上救援', '灾害应急', '危险区域接近'],
          video: 'https://www.youtube.com/embed/dQw4w9WgXcQ'
        }
      ]
    }
  },
  computed: {
    filteredBoats() {
      if (this.activeFilter === 'all') {
        return this.boats;
      }
      return this.boats.filter(boat => boat.type === this.activeFilter);
    }
  },
  mounted() {
    // 检查路由参数，判断是否需要显示特定产品
    const id = this.$route.params.id;
    if (id) {
      const boat = this.boats.find(b => b.id === id);
      if (boat) {
        this.$nextTick(() => {
          this.showDetails(boat);
        });
      }
    }
  },
  methods: {
    setFilter(filter) {
      this.activeFilter = filter;
    },
    getMainSpecs(boat) {
      // 根据产品类型获取关键参数
      const specKeys = {
        single: ['尺寸', '重量', '最大载重', '续航时间'],
        sampling: ['采样容量', '续航时间', '通信距离', '防护等级'],
        patrol: ['尺寸', '最大速度', '续航时间', '通信距离'],
        firefighting: ['水炮流量', '水炮射程', '水箱容量', '最大速度']
      };
      
      const type = boat.type || 'single';
      return specKeys[type] || Object.keys(boat.specifications).slice(0, 4);
    },
    getBadgeClass(type) {
      const classes = {
        single: 'bg-info',
        sampling: 'bg-success',
        patrol: 'bg-primary',
        firefighting: 'bg-danger'
      };
      return classes[type] || 'bg-secondary';
    },
    getTypeName(type) {
      const names = {
        single: '单人船',
        sampling: '采样船',
        patrol: '巡逻船',
        firefighting: '消防船'
      };
      return names[type] || '其他船艇';
    },
    showDetails(boat) {
      this.selectedBoat = boat;
      document.getElementById('boatModal').classList.add('show');
      document.getElementById('boatModal').style.display = 'block';
      document.body.classList.add('modal-open');
      const modalBackdrop = document.createElement('div');
      modalBackdrop.classList.add('modal-backdrop', 'fade', 'show');
      document.body.appendChild(modalBackdrop);
    },
    closeModal() {
      document.getElementById('boatModal').classList.remove('show');
      document.getElementById('boatModal').style.display = 'none';
      document.body.classList.remove('modal-open');
      const backdrop = document.querySelector('.modal-backdrop');
      if (backdrop) {
        backdrop.remove();
      }
    }
  }
}
</script>

<style scoped>
.system-page {
  min-height: 80vh;
}

.category-filter {
  margin-bottom: 2rem;
}

.product-card {
  transition: all 0.3s ease;
  border: none;
  box-shadow: 0 4px 15px rgba(0,0,0,0.07);
  overflow: hidden;
}

.product-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0,0,0,0.1);
}

.product-image {
  position: relative;
  overflow: hidden;
}

.product-image img {
  height: 220px;
  object-fit: cover;
  transition: all 0.5s ease;
}

.product-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: all 0.3s ease;
}

.product-card:hover .product-image img {
  transform: scale(1.05);
}

.product-card:hover .product-overlay {
  opacity: 1;
}

.product-badge {
  display: inline-block;
  padding: 0.35em 0.65em;
  font-size: 0.75em;
  font-weight: 500;
  line-height: 1;
  color: #fff;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: 0.25rem;
  margin-bottom: 0.75rem;
}

.specs {
  margin-top: 1rem;
}

.specs-item {
  margin-bottom: 0.5rem;
  font-size: 0.85rem;
}

.specs-label {
  color: var(--secondary-color);
  margin-right: 0.25rem;
}

.specs-value {
  font-weight: 500;
}

.feature-item {
  padding: 0.5rem;
  background-color: rgba(0,0,0,0.02);
  border-radius: 0.25rem;
  height: 100%;
}

.text-accent {
  color: var(--accent-color);
}

.spec-box {
  border: 1px solid rgba(0,0,0,0.05);
}

@media (max-width: 767.98px) {
  .product-image img {
    height: 180px;
  }
}
</style> 